
.reservation{
		position: relative;
		width: 100%;
		height: 100%;
}
.reservation-time{
		padding-left: 220px;
    padding-top: 20px;
    /*position: fixed;
    width: calc(100% - 40px);*/
    top: 0rem;
    left: 0;
    background: #fff;
    z-index: 1;
		border-bottom: 1px solid #eee;
}
.reservation-content{
		padding-top: 15px;
}
.reservation-time-left{
		display: inline-block;
    vertical-align: middle;
    width: calc(85% - 130px);
}
.reservation .time-left-item {
    display: inline-block;
    text-align: center;
    color: #999;
    padding: 0 3%;
    position: relative;
    height: .9rem;
}
.reservation .time-left-item span {
    display: block;
}
.reservation .time-left-item>span:nth-child(1){
		font-size: 1.5vw;
}
.reservation .time-left-item>span:nth-child(2){
		font-size: 1.8vw;
    padding-bottom: 3px;
}
.reservation .time-left-item .line{
		position: absolute;
    bottom: 0;
    width: 30%;
    background: #3c9bff;
    display: inline-block;
    height: 2px;
    left: 35%;
    display: none;
}
.reservation .time-left-item.on span{
		color: #3c9bff;
}
.reservation .time-left-item.on .line{
		display: block;
}
.reservation-time-right{
		display: -webkit-inline-box;
		display: -ms-inline-flexbox;
		display: inline-flex;
    vertical-align: middle;
    width: 15%;
}
.reservation-time-right img{
		width: auto;
    height: 30px;
    vertical-align: middle;
}
.reservation-time-right span{
		vertical-align: middle;
    margin-left: 10px;
    display: inline-block;
    line-height: 30px;
    color: #3c9bff;
    font-size: .32rem;
}
.reservation .content-left{
    padding: 0 10px 0 220px;
    vertical-align: top;
    color: #999;
}
.reservation .item-left{
		display: inline-block;
    width: 9%;
    text-align: center;
    font-size: 1.5vw;
    line-height: 69px;
}
.reservation .item-right{
		width: 88%;
    display: inline-block;
    vertical-align: top;
    white-space: nowrap;
    border-bottom: 1px solid #eee;
    position: relative;
}
.reservation .item-right>div {
    display: inline-block;
    line-height: .7rem;
    padding: 8px 0;
    height: .7rem;
    width: 21%;
    vertical-align: top;
}
.reservation .item-right .btn{
		width: 13%;
    padding: 8px 10px;
    padding-left: 0;
}
.reservation .patient {
		margin-left: 2%;
}
.reservation .btn span{
		width: 100%;
    text-align: center;
    border-radius: 5px;
    color: #3C9BFF;
    font-size: 1.5vw;
    display: inline-block;
}
.item-right>div span {
		margin-left: 10px;
}
.reservation .item-right .right-line{
	    position: absolute;
    width: 2px;
    height: 35px;
    background: #999;
    left: 0;
    top: 4px;
}
.reservation .name,.reservation .experts-name{
		color: #333;
}
.reservation .name{
		font-size: 1.6vw;
}
.reservation .content-list{
		width: 100%;
}
li.content-item {
    font-size: 1.5vw;
    margin-bottom: 15px;
}
.reservation .item-left{
		visibility: hidden;
}
.content-item>div:nth-child(1) .item-left{
		visibility: visible;
}
.reservation .item-text.done .status-text{
		color: #333;
}
.reservation .item-text.cancel .status-text,.reservation .item-text.unfinished .status-text{
		color: #d43c33;
}
.reservation .item-text.current .right-line{
		background: #3c9bff;
}
.reservation .item-text.unfinished .btn span{
		background: #eee;
		color:#999;
}
.reservation .item-text.unfinished .item-left{
		color: #333;
}
.reservation .item-time.current{
		color: #3c9bff;
}
.reservation .right-line.current{
		background: #3c9bff;
}
.btn .current{
		color: #fff !important;
		background: #3c9bff !important;
}
.reservation .subscribe-btn{
		display: inline-block;
		position: fixed;
    right: 0px;
   	bottom: .5rem;
}
.reservation .subscribe-btn img{
    width: 1.5rem;
    height: auto;
}
	/*状态*/
.btn span.Revisiting{
		color: #fff;
		background: #3C9BFF;
}
.btn span.Canceled{
		color: #999;
		background: #fff;
}
.btn span.Waiting{
		color: #FC9452;
}
.btn span.Finish{
		color: #333;
}
.btn span.cancelbtn{
		color: #FC9452;
    border: 1px solid #FC9452;
}
	/*tag*/
.doctor-atatus{
    padding: 3px 5px;
    font-size: 1.2vw;
    border-radius: 3px;
}
.doctor-atatus.online{
		color: #1FAD00;
    background: #efffe5;
}
.doctor-atatus.suspend{
		color: #ff3800;
    background: #ffd9cc;
}
.doctor-atatus.leave{
		color: #999;
    background: #eee;
}
.checkrecord{
		color: #3c9bff;
		border-bottom: 0 !important;
}
.checkrecord img{
		width: auto;
		height: 25px;
		vertical-align: middle;
		margin-left: 10px;
}
.checkrecord span{
		vertical-align: middle;
}
.win-check{
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		background: rgba(0,0,0,0.7);
		z-index: 12;
}
.check-content{
		position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: #fff;
    text-align: center;
}
.check-content img{
		width: 100%;
}
	/**/
.win-video{
	  position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
    top: 0;
    left: 0;
    z-index: 2;
}
.win-video-content{
    width: 70%;
    background: #fff;
    border-radius: 10px;
    font-size: .32rem;
    position: absolute;
    top: 50%;
    left: 20%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}
.video-content-left{
		width: 40%;
    text-align: center;
    padding-top: 45px;
    height: 100%;
    background: #ebf5ff;
    padding-bottom: 45px;
    border-radius: 10px 0 0 10px;
    min-height: 390px;
}
.win-video-content>div {
    display: inline-block;
    vertical-align: text-top;
}
.video-left-bottom{
		background: #fff;
    width: 80%;
    margin-left: 10%;
    border-radius: 7px;
    margin-top: 30px;
    padding-bottom: 30px;
}
.video-left-bottom>p{
		margin-bottom: 10px;
}
.vi-icon{
		width: auto;
		height: 1.1rem;
		margin-top: -0.4rem;
    border-radius: 50%;
}
.vd-nam{
		font-size: 1.8vw;
    font-weight: bold;
}
.vi-status{
    background: #e9f4de;
		color: #4b9705;
    padding: 3px 5px;
}
.video-content-roght{
    text-align: center;
    width: 60%;
    padding: 30px 0;
}
.win-video-content>div {
    display: inline-block;
    vertical-align: text-top;
}
.wait-time,.wait-time-text{
		color: #d43c33;
		font-size: 0.36rem;
}
.patient-mess{
		padding: 0px 20%;
}
.patient-mess>p{
		border-bottom: 1px solid #eee;
    padding: 20px 0;
    font-size: 0.7rem;
}
.patient-mess>p img{
		width: 1.15rem;
    height: 1.15rem;
    vertical-align: middle;
    margin-right: 15px;
    border-radius: 50%;
}
.wait-detail{
    padding: 20px 0 35px 0;
    font-size: .4rem;
}
.wait-detail>p span{
		color: #F59C31;
}
.wait-detail>p span:nth-child(1){
		font-size: 1rem;
}
.wait-success{
		padding: 30px 0;
    font-size: 1rem;
    color: #3C9BFF;
}
.wait-hint{
		text-align: center;
}
.wait-hint p{
		display: inline-block;
    width: 45%;
    background: #3C9BFF;
    color: #fff;
    line-height: 1rem;
    border-radius: 10px;
    font-size: .48rem;
}
.wait-btn>p{
   	display: inline-block;
    line-height: 1rem;
}
.canceljoin{
		width: 20%;
    border: 1px solid #F59C31;
    color: #F59C31;
    border-radius: 5px;
    font-size: .48rem;
    margin-right: 10%;
}
.joinroom{
		width: 45%;
    background: #3C9BFF;
    color: #fff;
    line-height: 1rem;
    border-radius: 5px;
    font-size: .48rem;
}
.vi-close{
		position: absolute;
    top: 10px;
    right: 15px;
}
.vi-close img{
		width: auto;
    height: .6rem;
}
	/*当日就诊*/
.navigation{
		border-bottom: 1px solid #eee;
    padding-left: 220px;
}
.navigation-day{
		font-size: .32rem;
}
.navigation-item{
		display: inline-block;
    font-size: .32rem;
    padding: 15px 20px;
}
.navigation-item.on{
		color: #3C9BFF;
    border-bottom: 2px solid #3C9BFF;
}
.doctor-content{
		padding-left: 220px;
}
.doctor-content-head{
		padding-top: 15px;
    border-bottom: 1px solid #eee;
    white-space: nowrap;
    overflow-y: hidden;
    overflow-x: auto;
}
.doctor-content-head>div {
    display: inline-block;
    vertical-align: bottom;
    width: 19%;
    text-align: center;
    height: 75px;
    position: relative;
    margin-left: -5%;
    color: #666;
}
.doctor-content-head>div:nth-child(1){
		margin-left: 0;
}
.doctor-circle{
		display: inline-block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    vertical-align: middle;
}
.doctor-circle.Visiting{
		background: #68FF00;
}
.doctor-circle.Left{
		background: #fc9452 ;
}
.doctor-circle.Suspended{
		background: #ccc;
}
.doctor-line{
		display: none;
}
.doctor-name-item.on{
		color:#3C9BFF;
}
.doctor-name-item.on .doctor-line{
		display: inline-block;
    width: 30px;
    height: 2px;
    background: #3C9BFF;
    position: absolute;
    bottom: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.doctor-content-bottom{
		padding: 20px 20px 0 20px;
}
.doctor-content-bottom>div {
    display: inline-block;
    vertical-align: top;
}
.doctor-bottom-left{
		width: 22%;
    text-align: center;
    margin-right: 2%;
    border: 1px solid #eee;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-bottom: 30px;
    color: #666;
}
.doctor-bottom-status>span{
    display: inline-block;
    width: 100%;
    padding: 5px 0;
}
.doctor-bottom-status .Visiting{
		background: #efffe5;
    color: #1FAD00;
}
.doctor-bottom-status .Suspended{
		background:rgba(204,204,204,0.1);
    color: #666;
}
.doctor-bottom-status .Left{
		background:rgba(252,148,82,0.1);
    color: #FC9452;
}
.doctor-bottom-headurl img{
		width: 1.2rem;
    height: 1.2rem;
    margin: 20px 0 20px 0;
    border-radius: 50%;
}
.doctor-bottom-name>span{
		color: #333;
    margin-right: 10px;
}
.doctor-bottom-hsp{
		margin: 10px 0;
		text-align: left;
    padding: 10px 10px 10px 10px;
}
.doctor-bottom-number>span{
		font-size: 1rem;
		color: #3C9BFF;
}
.doctor-bottom-right{
		width: 76%;
}
.doctor-list-item>div {
    display: inline-block;
    width: 21%;
    line-height: .7rem;
}
.doctor-box-sex>span{
		color: #666;
}
.doctor-box-sex>span:nth-child(1){
		color: #333;
}
.doctor-box-sex>span:nth-child(1),.doctor-box-sex>span:nth-child(2){
		margin-right: 10px;
}
.doctor-box-status{
		color: #666;
}
.doctor-box-status span{
		color: #333;
		margin-left: 5px;
}
.doctor-list-item .case-history{
		width: 15%;
		text-align: center;
}
.case-history span{
		vertical-align: middle;
		color: #3C9BFF;
}
.case-history img{
		margin-left: 10px;
		vertical-align: middle;
}
.doctor-list-item{
		border-bottom: 1px solid #eee;
    padding: 10px 0;
}
.doctor-box-operation span{
		display: inline-block;
    width: 85%;
    text-align: center;
    border-radius: 5px;
}
.doctor-box-operation span.anew{
		background: #3C9BFF;
		color: #fff;
}
.doctor-box-operation span.waiting{
		color: #FC9452;
}
.doctor-box-operation span.pending{
		color:#3C9BFF;
}
.doctor-item-office{
    display: block;
    margin-top: 5px;
    margin-bottom: 0px;
    font-size: .22rem;
}
.doctor-department{
		display: inline-block;
		margin-right: 5px;
		max-width: 60%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    vertical-align: middle;
}
.doctor-name-item.on .doctor-item-office{
		margin-top: 10px;
}
.doctor-item-content{
		position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    height: 75px;
}
.doctor-item-banner{
		height: 75px;
}
.doctor-item-banner img{
		width: 100%;
		height: 75px;
}
.doctor-name-item.on{
		height: 85px;
}
.doctor-name-item.on .doctor-item-office{
		font-size: .25rem;
}
.doctor-name-item.on .doctor-item-content{
		height: 85px;
}
.doctor-name-item.on .doctor-item-banner{
		height: 85px;
}
.doctor-name-item.on .doctor-item-banner img{
		 height: 85px;
}
	/**/
.win-cancel{
		position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0,0,0,0.6);
}
.win-cancel-content{
		position: absolute;
    width: 60%;
    left: 22%;
    background: #fff;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    border-radius: 10px;
    text-align: center;
    padding-top: .45rem;
}
.cancel-hint{
		font-size: 0.44rem;
    font-weight: bold;
}
.cancel-hint-text{
		padding: .5rem .2rem;
    font-size: .38rem;
    border-bottom: 1px solid #eee;
    color: #666;
}
.win-cancel-btn>div {
    display: inline-block;
    width: 50%;
    line-height: 1.08rem;
    font-size: .4rem;
}
.cancel-btn-left{
		border-right: 1px solid #eee;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    color: #666;
}
.cancel-btn-right{
		color: #3C9BFF;
}
